import styled from "styled-components";
import Link from "./Link";
import {useCallback, useEffect, useState} from "react";
import AddLink from "./AddLink";
import {extractFavicon} from "../util/FaviconExtractor";
import {DndProvider} from "react-dnd";
import {DragDropContext, Draggable, Droppable} from "react-beautiful-dnd";

const Wrapper = styled.div`

  max-width: 620px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

  padding: 30px;
`;
const LinkList = () => {

    const [links, setLinks] = useState([]);
    const [linksLastId, setLinksLastId] = useState(null);

    useEffect(() => {
        const links = JSON.parse(localStorage.getItem('links'));
        const linksLastId = Number(localStorage.getItem('linksLastId')) || 0;

        if (links) {
            setLinks(links);
        }
        setLinksLastId(linksLastId);

    }, []);

    const addLink = (name, url) => {
        let favicon = extractFavicon(url);
        const newLink = {
            id : linksLastId,
            name: name,
            url: url,
            favicon: favicon
        }

        const newLinksLastId = linksLastId + 1;

        setLinksLastId(newLinksLastId);
        setLinks([...links, newLink]);
        localStorage.setItem('links', JSON.stringify([...links, newLink]));
        localStorage.setItem('linksLastId', newLinksLastId);
    }

    const deleteLink = (id) => {
        const newLinks = links.filter(link => link.id !== id);
        setLinks(newLinks);
        localStorage.setItem('links', JSON.stringify(newLinks));
    }

    const handleEnd = (result) => {
if(!result.destination) return;

        const newLinks = links;
        const [reorderedItem] = newLinks.splice(result.source.index, 1);
        newLinks.splice(result.destination.index, 0, reorderedItem);
        setLinks(newLinks);
        localStorage.setItem('links', JSON.stringify(newLinks));
    }

    /**
     * todo: horizontal  vertical      
     *
     */
    return (
        <DragDropContext onDragEnd={handleEnd}>
            <Droppable droppableId="links" direction="horzontal">
                {(provided) => (
                    <Wrapper {...provided.droppableProps} ref={provided.innerRef}>
                        {links.map((link, index) => (
                            <Draggable
                                key={link.name}
                                draggableId={link.name}
                                index={index}
                            >
                                {(provided, snapshot) => (
                                    <Link key={index} link={link} id={link.name} deleteLink={deleteLink}
                                        provided={provided} snapshot={snapshot}
                                    />
                                )}
                            </Draggable>
                        ))} {provided.placeholder}
                        <AddLink addLink={addLink}/>
                    </Wrapper>
                )}
            </Droppable>
        </DragDropContext>
    );
}

export default LinkList;